html,body{
  width: 100vw;
  height: 100vh;
}
body{
  overflow-x: hidden;
}
.calendar-screen{
  display: flex;
  flex-flow: column;
  height: 100vh;
}

/* MODAL */


/* Modal */
.ReactModalPortal > div{
  opacity: 0;
}

.ReactModalPortal .ReactModal__Overlay {
  align-items: center;
  display: flex;
  justify-content: center;
  transition: opacity .2s ease-in-out;
  z-index: 999;
}

.modal-fondo {
  background-color: rgba(0,0,0, 0.1);
  backdrop-filter: blur(3px);
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  position: fixed;
}

.ReactModalPortal .ReactModal__Overlay--after-open {
  opacity: 1;
}

.ReactModalPortal .ReactModal__Overlay--before-close {
  opacity: 0;
}

.modal {
  background: white;
  border-radius: 5px;
  color: rgb(51, 51, 51);
  display: inline;
  max-height: 620px;
  max-width: 500px;
  outline: none;
  padding: 10px;
}

.react-datetime-picker input:focus{
  outline: none;
}
.react-datetime-picker__wrapper{
  border: none !important;
}

.fab{
  border-radius: 100%;
  bottom: 25px;
  font-size: 30px;
  padding: 25px;
  position: fixed;
  right: 25px;
  color: white;
  box-shadow: 1px 2px 8px #666;
}

.fab-danger{
  bottom: 25px;
  font-size: 10px;
  position: fixed;
  left: 25px;
  box-shadow: 1px 2px 8px #666;
}
